<template>
  <div>
    <!--  -->
    <breadNav :nowLocation="nowLocation"></breadNav>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="operate" size="small">
      <el-form-item label>
        <el-input-number v-model="formInline.size" :min="0" :max="500" label=""></el-input-number>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="addMore"></el-button>
        <el-button type="primary" @click="exportExcel"></el-button>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="renewType" placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="3"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="renew"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-table
      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="deviceId" label="id" min-width="100" width="200"></el-table-column>
      <el-table-column prop="deviceNo" label="" min-width="100" width="200"></el-table-column>
      <el-table-column prop="rootType" label="root" min-width="100" width="200"></el-table-column>
      <el-table-column prop="gnkg" label="" min-width="100" width="200"></el-table-column>
      <el-table-column prop="activeDate" label="" min-width="100" width="200"></el-table-column>
      <el-table-column prop="expireTime" label="" min-width="100" width="200"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <!-- <el-button size="mini" @click="handleChange(scope.$index, scope.row)"></el-button> -->
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="page">
      <!-- <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="10"
        layout="prev, pager, next, jumper"
        :total="total"
      ></el-pagination>-->
    </div>
    <!--  -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible">
      <el-form :model="dialogForm" v-if="dialogTitle==='' ">
        <!--  -->
        <el-row>
          <el-form-item label="root" label-width="120px">
            <el-select v-model="dialogForm.rootStatus" placeholder="ROOT /ROOT">
              <el-option label="root" value="1"></el-option>
              <el-option label="root" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" label-width="120px">
            <el-checkbox-group v-model="dialogForm.gnkg" size="middle">
              <el-checkbox-button v-for="index in gnOptions" :label="index" :key="index">{{index}}</el-checkbox-button>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="" label-width="120px">
            <el-input-number v-model="dialogForm.deviceSize" :min="1" :max="500"></el-input-number>
          </el-form-item>
        </el-row>
      </el-form>
      <!--  -->
      <el-form :model="dialogForm2" v-if="dialogTitle===''">
        <el-row>
          <el-form-item label="" label-width="120px">
            <el-checkbox-group v-model="dialogForm2.gnkg" size="middle">
              <el-checkbox-button v-for="index in gnOptions" :label="index" :key="index">{{index}}</el-checkbox-button>
            </el-checkbox-group>
          </el-form-item>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="ok()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { http } from "../../api/http";
import global from "../../util/globalFunction.js";
import { all } from "q";
export default {
  components: {
    breadNav: () => import("../../components/common/bread.vue")
  },
  data() {
    return {
      nowLocation: [""],
      formInline: { size: "" },
      tableData: [],
      multipleSelection: [],
      renewType: "1",
      currentPage: 1,
      total: 0,
      dialogFormVisible: false,
      dialogTitle: "",
      dialogForm: {
        rootStatus: "",
        gnkg: [],
        deviceSize: ""
      },
      dialogForm2: {
        gnkg: []
      },
      gnOptions: [1, 2, 3, 4, 5, 6, 7, 8]
    };
  },
  mounted() {
    this.getDeviceList();
  },
  methods: {
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {},
    handleCurrentChange(val) {},
    getDeviceList() {
      http("/manager/deviceList", "post", this.$route.query).then(res => {
        for (let val of res) {
          val.rootType == 1
            ? (val.rootType = "root")
            : (val.rootType = "root");
          val.activeDate == null ? (val.activeDate = "") : val.activeDate;
        }
        this.tableData = res;
        this.total = res.length;
      });
    },
    search() {},
    renew() {
      if (this.multipleSelection.length == 0) {
        this.$message.warning("");
      } else {
        let deviceIds = [];
        for (let val of this.multipleSelection) {
          deviceIds.push(val.deviceId);
        }
        let json = {
          deviceIds: deviceIds.join(","),
          renewalType: this.renewType
        };
        http("/manager/renewalDevice", "post", json).then(res => {
          this.$message.success("");
          this.getDeviceList();
        });
      }
    },
    addMore() {
      // this.dialogFormVisible = !this.dialogFormVisible;
      // this.dialogTitle = "";
      if (this.formInline.size == 0) {
        this.$message.warning("0");
      } else {
        let obj = {
          comId: this.$route.query.comId,
          size: this.formInline.size
        };
        http("/manager/addDeviceSize", "post", obj).then(res => {
          this.$message.success("" + obj.size + "");
          this.getDeviceList();
        });
      }
    },
    exportExcel() {
      http("/file/exportDevice", "get", this.$route.query, "blob");
    },
    ok() {
      if (this.dialogTitle == "") {
        let arr = [
          {
            gnkg: global.binary(this.dialogForm.gnkg),
            type: this.dialogForm.rootStatus,
            size: this.dialogForm.deviceSize
          }
        ];
        let json = {};
        this.createJson(json, arr);
        http("/manager/createCom", "post", json).then(res => {});
      } else if (this.dialogTitle == "") {
        alert("");
      }
      // close modal
      this.dialogFormVisible = !this.dialogFormVisible;
    },
    createJson(json, arr) {
      for (let index in arr) {
        json["root[" + index + "].size"] = arr[index].size;
        json["root[" + index + "].type"] = arr[index].type;
        json["root[" + index + "].gnkg"] = arr[index].gnkg;
      }
      return json;
    },
    handleChange(index, row) {
      this.dialogTitle = "";
      this.dialogFormVisible = !this.dialogFormVisible;
      this.dialogForm2.gnkg = global.original(row.gnkg);
    },
    handleDelete(index, row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        http("/manager/deleteDevice", "get", { id: row.deviceId }).then(res => {
          this.$message.success("");
          this.getDeviceList();
        });
      });
    }
  }
};
</script>

<style lang="less">
.operate {
  margin-top: 10px;
}
.page {
  margin-top: 10px;
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  border-bottom: 1px solid #ccc;
}
</style>

